.keep-in-touch-link a {
  display: block;
  text-decoration: none;
  text-align: center;
  margin: 0 5px; }
  .keep-in-touch-link a:hover, .keep-in-touch-link a:focus, .keep-in-touch-link a:active {
    text-decoration: underline; }

.keep-in-touch-link img,
.keep-in-touch-link span {
  display: block;
  margin: 10px auto 0; }

.keep-in-touch-link img {
  width: 80px; }



